.node--type-person {
  .person--name {
    font-size: 52px;
  }

  .person--contact {
    .label {
      display: inline-block;
      margin-bottom: 5px;
      font-size: 24px;
      line-height: 32px;
    }

    .person--tel, .person--email {
      .field__item {
        color: $color-orange;
        text-decoration: none;
        font-size: 16px;
        line-height: 24px;

        &::before {
          font-family: $font-awesome;
          font-size: 18px;
          padding-right: 5px;
        }
      }
    }

    .person--tel {
      .field__item {
        &::before {
          content: "\f095";
        }
      }
    }

    .person--email {
      .field__item {
        &::before {
          content: "\f0e0";
        }
      }
    }
  }

  .field--name-field-media {
    margin-top: 25px;
    @media #{$tablet-min}{
      margin-top: 0;
    }

    img {
      @media #{$tablet-min}{
        width: 100%;
        object-fit: cover;
      }
    }
  }
}